/* App.css */
.App {
  text-align: center;
}

.navbar {
  background-color: #333;
  padding: 10px;
}

.navbar-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: space-around;
}

.navbar-item {
  display: inline-block;
  margin-right: 10px;
  color: #fff;
  font-size: 18px;
  cursor: pointer;
  transition: color 0.3s ease-in-out;
}

.navbar-item:hover {
  color: #ff4500;
}

h1 {
  font-size: 24px;
  margin-top: 20px;
}
